'use client';

import { useState } from 'react';
import Link from 'next/link';

export default function WalletPage() {
    const [totalEarnings] = useState(3240.00);
    const [paymentHistory] = useState([
        {
            id: '1',
            date: 'April 28, 2024',
            description: 'Receive Delivery',
            amount: 120.00,
            type: 'credit'
        },
        {
            id: '2',
            date: 'April 21, 2024',
            description: 'Received',
            amount: 68.00,
            type: 'credit'
        },
        {
            id: '3',
            date: 'April 14, 2024',
            description: 'Payment',
            amount: -43.00,
            type: 'debit'
        },
        {
            id: '4',
            date: 'April 14, 2024',
            description: 'Visa ••••1234',
            amount: -56.00,
            type: 'debit'
        }
    ]);

    return (
        <div className="min-h-screen bg-black text-white flex flex-col">
            {/* Status Bar */}
            <div className="flex justify-between items-center p-4 pt-12 text-sm">
                <span>9:41</span>
                <div className="flex items-center gap-1">
                    <div className="flex gap-1">
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M2 17h20v2H2v-2zm1.15-6.05L4 9.47l.85 1.48H5c.55 0 1 .45 1 1s-.45 1-1 1-.45-.45-.45-1zm5.5 2.5H9c.55 0 1-.45 1-1s-.45-1-1-1-.45.45-.45 1z" />
                    </svg>
                    <div className="w-6 h-3 border border-white rounded-sm">
                        <div className="w-4 h-1.5 bg-white rounded-sm m-0.5"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2Z" />
                    </svg>
                </div>
            </div>

            {/* Header */}
            <div className="text-center px-8 mb-8">
                <h1 className="text-4xl font-bold mb-2">Pickom</h1>

                <h2 className="text-3xl font-bold mt-8">Wallet</h2>
            </div>

            {/* Total Earnings */}
            <div className="px-8 mb-8">
                <div className="text-center">
                    <div className="text-5xl font-bold text-white mb-2">
                        +€{totalEarnings.toLocaleString('en-US', { minimumFractionDigits: 2 })}
                    </div>
                    <p className="text-gray-400 text-lg">Total Earnings (Picker)</p>
                </div>
            </div>

            {/* Payment History */}
            <div className="flex-1 px-8">
                <h3 className="text-2xl font-bold text-white mb-6">Payment History</h3>

                <div className="space-y-4">
                    {paymentHistory.map((payment) => (
                        <div key={payment.id} className="bg-gray-900 rounded-lg p-4">
                            <div className="flex items-center justify-between">
                                <div>
                                    <div className="text-white font-medium mb-1">{payment.date}</div>
                                    <div className="text-gray-400 text-sm">{payment.description}</div>
                                </div>
                                <div className={`text-right ${payment.type === 'credit' ? 'text-green-500' : 'text-white'
                                    }`}>
                                    <div className="text-lg font-semibold">
                                        {payment.type === 'credit' ? '+' : ''}€{Math.abs(payment.amount).toFixed(2)}
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Back Button */}
            <div className="p-8">
                <Link
                    href="/profile"
                    className="w-full bg-gray-800 text-white py-4 rounded-full text-lg font-medium text-center block hover:bg-gray-700 transition-colors"
                >
                    Back to Profile
                </Link>
            </div>

            {/* Bottom Indicator */}
            <div className="flex justify-center pb-6">
                <div className="w-32 h-1 bg-white rounded-full"></div>
            </div>
        </div>
    );
} 